export const SET_COOLERS = 'SET_COOLERS';

export function setCoolers(coolers) {
    return {
        type:SET_COOLERS,
        coolers
    }
}


export function fetchCoolers() {
    return dispatch => {
        fetch('/api/coolers')
            .then(res => res.json())
            .then(data => dispatch(setCoolers(data.coolers)))
        ;
    }
}




